.btn:hover{
    background-color: #45a049;
    color: white;   
}
.btn{
    background-color: #04AA6D; 
    border: none;
    color: white;
    padding: 10px 24px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    transition-duration: 0.4s;
    border-radius: 12px;
}
.seat-btn:active{
    background-color: #4CAF50; 
    color: white;
       
}
.seat-btn:hover{
    background-color: #8d25c5;
    color: white;   
}